import React from 'react';
import { Button } from '@mui/material';
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import { Grid } from '@mui/material';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';

import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import Avatar from '@mui/material/Avatar';
import PersonIcon from '@mui/icons-material/Person';


import { formatEthFromWei } from '../Formatters';
import { TeamInfoResponse, _teamResponse } from '../assets/abi/farm-contract';
import { ClassNames } from '@emotion/react';
//import { GetSiteInfoResponse } from '../assets/abi/farm-contract';

interface TeamViewProps {
  style?: React.CSSProperties;
  className?: string;
  teamId: string;
  defaultname: string;
}

const bull = (
    <Box
      component="span"
      sx={{ display: 'inline-block', mx: '2px', transform: 'scale(0.8)' }}
    >
      •
    </Box>
  );

class TeamView extends React.Component<TeamViewProps> {
  className?: string;
  style?: React.CSSProperties;
  teamId: string;
  defaultname: string;

  state = {
    members: [],
    nicknames: [],
    owner: "",
    id: "",
    created_at: "",
    name: "",
    is_referral_team: false,

    showMembers: false,
    teamName: ""
  }
  baseState: any;
  _stateInterval: any;

  constructor(props: TeamViewProps) {
      super(props);

      this.baseState = this.state;

      // properties
      this.className = props.className;
      this.style = props.style;
      this.teamId = props.teamId;
      this.defaultname = props.defaultname;
  }

  componentDidMount = () => { 

    this.checkState();
    if(this._stateInterval){
      clearInterval(this._stateInterval);
    }
    this._stateInterval = setInterval(this.checkState, window.dataRefreshInterval * 2);

    this.state.teamName = this.defaultname;
  }

  componentWillReceiveProps(props: TeamViewProps) { 
    //console.log("componentWillReceiveProps", props);
    this.teamId = props.teamId;
    this.defaultname = props.defaultname;
    this.setState({
      teamName: this.defaultname           
    });
  }

  componentWillUnmount = () => { 


  }

  onClick_AddMember = () => {

  }

 

  checkState = () => {  
    if(window.connected !== true){
      this.setState(this.baseState);
      return;
    }    
    //console.log("checkState", "teamId", this.teamId);

    if(this.teamId != ""){
      window.farm_contract.methods.teamInfo(this.teamId).call().then((result: TeamInfoResponse) => {
        //console.log("teamId", this.teamId, result);

        let teamName = "Team " + result._team.id; // default = Team + id
        if(result._team.is_referral_team === true){
          teamName = this.defaultname; // if team is ref-team...
        }
        if(result._team.name.length > 0){
          teamName = result._team.name; // name override
        }

        teamName = teamName;

        this.setState({
          nicknames: result.nicks,
          members: result._team.members,
          showMembers: result._team.members.length > 0,
          owner: result._team.owner,
          id: result._team.id,
          created_at: result._team.created_at,
          name: result._team.name,
          is_referral_team: result._team.is_referral_team,
          teamName: teamName           
        });

        //console.log("result.name", result.name, result.name.length)
      });
    }else{
      this.setState(this.baseState);
    }
  }  

  render = () => {

    let memberList;
    if (this.state.showMembers) { 
        memberList = this.state.members.map((walletAddress, index)=>{
        let nick = this.state.nicknames[index] as string;
        if(this.state.owner == walletAddress){
          nick = nick + " (owner)";
        }
        return <ListItem key={walletAddress}>
          <ListItemAvatar>
            <Avatar>
              <PersonIcon />
            </Avatar>
          </ListItemAvatar>
          <ListItemText primaryTypographyProps={{className: "truncate-text"}}  primary={walletAddress} secondary={nick} />
        </ListItem>
      });    
    } else {
      memberList = <ListItem>
      <ListItemText primaryTypographyProps={{className: "truncate-text"}}  primary="User has no members yet." secondary="" />
    </ListItem>;  
    }

    return (
      <Paper className={this.className} style={this.style}>
        <Typography sx={{ fontSize: 16, marginLeft: "16px"/*, marginTop: 1 */}} color="secondary" gutterBottom>
          {this.state.teamName}:
        </Typography>

        <Typography sx={{display: (this.state.created_at != "0")?"block":"none", fontSize: 14, marginLeft: "16px"/*, marginTop: 1 */}} color="secondary" gutterBottom>
          Team-Id: {this.state.id}
        </Typography>

        <List sx={{
          width: '100%',
          bgcolor: 'background.paper',
          position: 'relative',
          overflow: 'auto',
          maxHeight: "350px"
        }}>
          {memberList}
        </List>
      </Paper>
    );
  }
}

export default TeamView;
