import diamondteamlogo from "./assets/img/mydiamondteam-fire.png";

import {ContractContext, ContractInfoResponse, PayoutOfResponse, PoolTopInfoResponse, UserDirectTeamsInfoResponse, UserInfo2Response, UserInfoResponse, UserInfoTotalsResponse} from './assets/abi/farm-contract';
