const contractAddress: string = '0x24436520c319695E8c435Ea32Aa275f55E2a3F07'; // release on mainnet 




declare global {
  interface Window {
    dataRefreshInterval: number,
    farm_contract: ContractContext;
    web3: Web3;
    walletConnectProvider: WalletConnectProvider,
    ethereum: any;
    contractAddress: string;
    contract_abi: AbiItem[]
    walletAddress: string;
    connected: boolean;
    refferalAddress: string; // the ref address that invited me
    allowance: number;
  }
}

interface AppProps {
  style?: React.CSSProperties;
}

const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.h3,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));

const modalStyle = {
  position: 'absolute' as 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: "70%",
  height: "60%",
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
  color: "lightgrey",
  overflow: "auto",
  opacity: 0.95
};

window.dataRefreshInterval = 3000;

class App extends React.Component<AppProps> {
  
  state = {
    balance: "",
    sponsorAddress: "",
    modalOpen: false,
    referralFromUrl: "",
    totalDeposits: "",
    withdrawnByMembers: "",
    topSponsorRewardPool: "",
    topSponsorRewardPoolCountdown: "",
    maximumAmountFromTopReferrer: "",
    referredBy: "?",
    myUpline: "", // same like referreedBy but wallet address only. without text ...
    yourAddress: "?",
    nextIncomeCountdown: "00:00:00",
    incomeLimitRemain: "",
    dailyIncome: "",
    dailyIncomeExpected: 0,
    myCurDeposit: "",
    myCurWithdraw: "",
    directReferralIncome: "",
    matchingBonus: "",
    topSponsorRewards: "",
    unwithdrawnIncome: "",
    totalAmountOfDepositedBNB: "",
    totalAmountOfReceivedBNB: "",
    totalReturnOfInvestment: "",
    personalInvitedPartners: "0",
    totalNumberOfPartners: "0",
    allDownlinesInvestments: "",
    projectWallet: "",
    totalUsers: "?",
    totalReinvested: "",
    withdrawPossible: false,
    myAirdrops: "",
    myTotalReceivedAirdrops: "",
    totalAirdrops: "",
    myLastAirdropSent: "",
    referral_team: "",
    upline_team: "",
    nickname: "",
    countdownVisible: false
  }

 // private defaultSponsorAddress: string = "0xb43aa594c3a40b6b788184925cd00f20b6f72ccf";
  //private defaultSponsorAddress: string = "0x6Cb264194AF62ECdE83c3E13AEfCb7E2a70B0bfE";
  private defaultSponsorAddress: string = "0xfDC01397731d3280bBB5e2A136381297962C87d2";

  private amountInput: React.RefObject<StandardTextFieldProps>;
  private sponsorInput: React.RefObject<StandardTextFieldProps>;

  baseState: any;
  _stateInterval: any;

  constructor(props: AppProps) {
    super(props);

    this.baseState = this.state;
    // properties

    this.amountInput = React.createRef();
    this.sponsorInput = React.createRef();

  }

  private _modalInterval: any = null;
  private CountDownDate: Date = new Date();

  componentDidMount = () => { 
    window.contractAddress = contractAddress;
    window.contract_abi = abi as AbiItem[];

    //const urlParams = new URLSearchParams(window.location.search);
    /*let unlock = urlParams.get('unlock');
    if(unlock != "true"){
      this._modalInterval = setInterval(this.handleModal, 1000);
    }else{
      this.state.countdownVisible = false;
    } */   
    

    this.checkState();
    if(this._stateInterval){
      clearInterval(this._stateInterval);
    }
    this._stateInterval = setInterval(this.checkState, window.dataRefreshInterval);
  }


  componentWillUnmount = () => { 
    
  }

  handleModal = () => {
    let countDownDateUTC = new Date();
    countDownDateUTC.setUTCDate(29);
    countDownDateUTC.setUTCMonth(11);
    countDownDateUTC.setUTCFullYear(2021);
    countDownDateUTC.setUTCHours(14);
    countDownDateUTC.setUTCMinutes(0);
    countDownDateUTC.setUTCSeconds(0);

    this.CountDownDate = countDownDateUTC;

    if((new Date()).getTime() < countDownDateUTC.getTime()){
      this.setModalOpen();
      this.setState({countdownVisible: true});
    }else{
      if(this._modalInterval != null ){
        clearInterval(this._modalInterval);
        this._modalInterval = null;
        this.setState({countdownVisible: false});
        this.setModalClose();
      }
    }
  }

  checkState = () => {  

    const urlParams = new URLSearchParams(window.location.search);
    let refAddress = urlParams.get('ref');
    //
    if(refAddress != null){

      window.refferalAddress = refAddress;
      this.setState({
        referralFromUrl: "Referral bonus from your investment will go to " + window.refferalAddress
      });
      //console.log("refAddress", window.refferalAddress);

      if(this.sponsorInput.current != null){
        //this.sponsorInput.current.value = refAddress // ref address in sponsor txt
        this.setState({sponsorAddress: window.refferalAddress});
      }

    }  

    if(window.connected !== true){
      this.setState(this.baseState);
      return;
    }

    this.setState({
      yourAddress: window.walletAddress,
    })


     window.farm_contract.methods.project().call().then((result: string) => {
      if(!window.web3.utils.isAddress(this.state.projectWallet)){
        this.setState({
          projectWallet: result
        });
        if(this.sponsorInput.current != null){
         //this.sponsorInput.current.value = result;
        // this.forceUpdate();
        }
      }
    }).catch((err:any) => {
      console.log(err);
    }); 
    
    // contract info
    window.farm_contract.methods.contractInfo().call().then((result: ContractInfoResponse) => {

      let topSponsorRewardPoolCountdown = new Date((parseInt(result._pool_last_draw)+86400) *1000  );

      this.setState({
        totalUsers: result._total_users, //new
        totalDeposits: result._total_deposited,
        withdrawnByMembers: result._total_withdraw,
        //totalReinvested: result._total_reinvested, // new
        topSponsorRewardPoolCountdown: topSponsorRewardPoolCountdown.toLocaleString(),
        maximumAmountFromTopReferrer: result._pool_lider,
        topSponsorRewardPool: result._pool_balance,
        totalAirdrops: result._total_airdrops
      });
    }).catch((err:any) => {
      console.log(err);
    });

    // poolTopInfo
    /*
    window.farm_contract.methods.poolTopInfo().call().then((result: PoolTopInfoResponse) => {
      this.setState({
        topSponsorRewardPool: result.deps[0].toString()
        
      });
    }).catch((err:any) => {
      console.log(err);
    });*/

    window.web3.eth.getBalance(window.walletAddress).then(result => {
      this.setState({balance: result});
    }).catch((err:any) => {
      console.log(err);
    });

    // userInfo
    window.farm_contract.methods.userInfo(window.walletAddress).call().then((result: UserInfoResponse) => {
      let nextIncomeCountdown = result.checkpoint;
      if(nextIncomeCountdown == "0"){
        nextIncomeCountdown = "Will show after deposit";
      }else{
        nextIncomeCountdown = new Date((parseInt(result.checkpoint) + 86400 ) * 1000).toLocaleString();
      }

      this.setState({
        referredBy: result.upline == "0x0000000000000000000000000000000000000000"? "Will show after deposit": result.upline,
        myUpline: result.upline,
        nextIncomeCountdown: nextIncomeCountdown,
        myCurDeposit: result.deposit_amount,
        myCurWithdraw: result.payouts,
        directReferralIncome: result.direct_bonus,
        matchingBonus: result.match_bonus,
        topSponsorRewards: result.pool_bonus,
        unwithdrawnIncome: parseFloat(window.web3.utils.fromWei(result.direct_bonus)) + parseFloat(window.web3.utils.fromWei(this.state.dailyIncome)) + parseFloat(window.web3.utils.fromWei(result.match_bonus)) + parseFloat(window.web3.utils.fromWei(result.pool_bonus))
      });
    }).catch((err:any) => {
      console.log(err);
    });

    // payoutOf
    window.farm_contract.methods.payoutOf(window.walletAddress).call().then((result: PayoutOfResponse) => {

      let withdrawPossible = this.state.withdrawPossible;
      if(window.web3.utils.fromWei(result.payout) != "0"){
        withdrawPossible = true;
      }

      this.setState({
        incomeLimitRemain: 3.65 * parseFloat(window.web3.utils.fromWei(this.state.myCurDeposit)) - parseFloat(window.web3.utils.fromWei(this.state.myCurWithdraw)),
        dailyIncomeExpected: parseFloat(window.web3.utils.fromWei(this.state.myCurDeposit)) / 100 * 1.5,
        dailyIncome: result.payout,
        withdrawPossible: withdrawPossible
      });


    }).catch((err:any) => {
      console.log(err);
    });

    // userInfoTotals
    window.farm_contract.methods.userInfoTotals(window.walletAddress).call().then((result: UserInfoTotalsResponse) => {

      this.setState({
        totalAmountOfDepositedBNB: result.total_deposits,
        totalAmountOfReceivedBNB: result.total_payouts,
        totalReturnOfInvestment: parseFloat(window.web3.utils.fromWei(result.total_payouts)) / parseFloat(window.web3.utils.fromWei(result.total_deposits)) * 100,
        personalInvitedPartners: result.referrals,
        allDownlinesInvestments: result.total_downline_deposit,
        totalNumberOfPartners: result.total_structure,
        myTotalReceivedAirdrops: result.airdrops_received,
        myAirdrops: result.airdrops_total
      });
    }).catch((err:any) => {
      console.log(err);
    });

    // userInfo2
    window.farm_contract.methods.userInfo2(window.walletAddress).call().then((result: UserInfo2Response) => {
      let lastSent = "never";
      if( result.last_airdrop != "0"){
        lastSent = new Date(parseInt(result.last_airdrop) * 1000 ).toLocaleString()
      }
      this.setState({
        myLastAirdropSent: lastSent,
        nickname: result.nickname
      });
    }).catch((err:any) => {
      console.log(err);
    });

    // userInfo2
    window.farm_contract.methods.userDirectTeamsInfo(window.walletAddress).call().then((result: UserDirectTeamsInfoResponse) => {
      this.setState({
        referral_team: result.referral_team_exists?result.referral_team:"",
        upline_team: result.upline_team_exists?result.upline_team:""
      });
    }).catch((err:any) => {
      console.log(err);
    });
  }


  setModalOpen = () => {
    this.setState({modalOpen: true});
  }

  setModalClose = () => {
    clearInterval(this._modalInterval);
    this._modalInterval = null;
    this.setState({modalOpen: false});
  }

  copyRefToClipboard = () => {
    let referralPart = (this.state.nickname)?this.state.nickname:window.walletAddress;
    let refAddress = window.location.origin + "?ref=" + referralPart;
    copy(refAddress);
  }

  onClick_VerifiedContract = () => {
    let a = document.createElement("a");
    a.target = "_blank";
    a.href = "https://bscscan.com/address/"+ window.contractAddress +"#code";
    //a.href = "https://testnet.bscscan.com/address/"+ window.contractAddress +"#code";
    a.click();
  }

  onClick_Telegram = () => {
    let a = document.createElement("a");
    a.target = "_blank";
    a.href = "https://t.me/MyDeFi_Team";
    a.click();
  }

  onClick_Audit = () => {
    //alert("work in progress. we will have the audit soon.");
    //return;
    let a = document.createElement("a");
    a.target = "_blank";
    a.href = "https://cybercrimeshield.org/secure/my-diamond-team/";
    a.click();
  }

  onClick_DownloadLitepaper = () => {
    let a = document.createElement("a");
    a.target = "_blank";
    a.href = "https://mydiamondteam.online/litepaper.pdf";
    a.click();
  }

  onClick_Invest = () => {
    if(!this.amountInput.current || !this.sponsorInput.current){
      return;
    }

    
      let ref = window.refferalAddress;
      if (!window.web3.utils.isAddress(ref)){
        ref=this.sponsorInput.current.value as string;
        //console.log("this.state.myUpline", this.state.myUpline)
        if(this.state.myUpline == "" || this.state.myUpline == "0x0000000000000000000000000000000000000000"){
          if(ref.length > 0 && !window.web3.utils.isAddress(ref) ){
            // no address, but value present --> check if its a valid nickname
            window.farm_contract.methods.getAddressToNickname(ref).call().then((result: string) => {
              console.log("checked nickname ", ref, " and got address ", result);
              if( result != "0x0000000000000000000000000000000000000000"){

                // invest with nickname
                if(!this.amountInput.current){
                  return;
                }
                console.debug("invest with nickname ", ref);

                window.farm_contract.methods.depositWithNickname( ref )
                  .send({ value: window.web3.utils.toWei(this.amountInput.current.value as string), from:window.walletAddress}).then((response:any) => {
              
                  }).catch((err:any) => {
                    console.log(err)
                  }
                );

              }else{
                alert("Invalid Sponsor Address");
                return;
              }
            });  
            
            return; // exit here. async forward
          }else if(ref.length == 0 ){
            if(window.confirm("You cannot join without a sponsor. would you like to use the project's default sponsor to join?") == true){

                this.setState({sponsorAddress: this.defaultSponsorAddress});

            }else{
              return;
            }
          }else{
            if(!window.web3.utils.isAddress(ref) || ref == window.walletAddress){
              alert("Sponsor Addres not valid");
              return;
            }
          }
        }
      }
      if(ref.length == 0){
        //ref = this.state.projectWallet;
        ref = this.defaultSponsorAddress;
        //console.log("Invest with Project Wallet as Sponsor.");
      }    

      // invest with upline address
      if(!this.amountInput.current){
        return;
      }

      console.debug("invest with address ", ref);
      window.farm_contract.methods.deposit( ref )
        .send({ value: window.web3.utils.toWei(this.amountInput.current.value as string), from:window.walletAddress}).then((response:any) => {
    
        }).catch((err:any) => {
          console.log(err)
        }
      );
  }

  onClick_Reinvest = () => {
    window.farm_contract.methods.reinvest().send({from: window.walletAddress}).then((result: TransactionReceipt) => {
      alert("Re-Invest Transaction Submitted.")
    }).catch((err:any) => {
      console.log(err)
    });
  }

  onClick_Withdraw = () => {
    window.farm_contract.methods.withdraw().send({from: window.walletAddress}).then((result: TransactionReceipt) => {
      alert("Withdraw Transaction Submitted.")
    }).catch((err:any) => {
      console.log(err)
    });
  }



  //  Main Layout
  //
  render = () => {
    return (
      <Box sx={{ flexGrow: 1 }} style={{width: "80%", margin: "auto", marginTop: "32px"}}>
        <Grid id="mainApp" container spacing={2} sx={{display: this.state.countdownVisible? "none":""}}>

          <Grid item xs={12} md={3}>
            <Paper className={"minHeight"} style={{padding: "16px", paddingTop: "16px"}}>
              <div style={{display: "flex", justifyContent: "center", alignItems:"center"}}>
                <img style={{width: "70%"}} src={diamondteamlogo}/>
              </div>
          
              <ConnectButton nickname={this.state.nickname} />

              <Typography sx={{ fontSize: 12, color: "orange" }} color="text.secondary" gutterBottom>
                {this.state.referralFromUrl}
              </Typography>

              <Grid spacing={1} container>
                <Grid item xs={12} md={8} sx={{p: 0}}>
                  <Button fullWidth={true} variant={"text"} color={"secondary"} sx={{my: 1, marginRight: 1}} style={{display: (this.state.totalAmountOfDepositedBNB == "0" || this.state.totalAmountOfDepositedBNB == "")? "none":""}} size="small" onClick={this.copyRefToClipboard}><ContentCopyIcon style={{height: "30px", marginRight: "8px"}}></ContentCopyIcon>Copy Referral Link</Button>
                </Grid>
                <Grid item xs={12} md={4} >
                  <SettingsDlg style={{display: (this.state.totalAmountOfDepositedBNB == "0" || this.state.totalAmountOfDepositedBNB == "")? "none":""}} nickname={this.state.nickname}></SettingsDlg>
                </Grid>
              </Grid>

              <Typography sx={{ fontSize: 14, marginTop: "16px" }} color="text.secondary" gutterBottom>
                My Deposits:
              </Typography>
              <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
                  {formatEthFromWei(this.state.myCurDeposit, 6)} BNB
              </Typography>

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                365% Income Limit Remain:
              </Typography>
              <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
                  {formatNumber(this.state.incomeLimitRemain, 6)} BNB
              </Typography>

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Unwithdrawn Income:
              </Typography>
              <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
                  {formatNumber(this.state.unwithdrawnIncome, 6)} BNB
              </Typography>      

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Income Withdrawn To Wallet:
              </Typography>
              <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
                  {formatEthFromWei(this.state.myCurWithdraw, 6)} BNB
              </Typography>     

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
              Total Return on Investment:
              </Typography>
              <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
                  {formatNumber(this.state.totalReturnOfInvestment, 6)} %
              </Typography>  

              <Grid container spacing={1}>
                <Grid item xs={12} md={4} sx={{p: 0}}>
                <Button sx={{my: 1}} disabled={!this.state.withdrawPossible} onClick={this.onClick_Withdraw} size="small" variant="contained"fullWidth={true}>withdraw</Button>
                </Grid>
                <Grid item xs={12} md={8} >
                <Button sx={{my: 1}} disabled={!this.state.withdrawPossible} onClick={this.onClick_Reinvest} size="small" variant="contained"fullWidth={true} color={"secondary"}>reinvest (+5% bonus)</Button>
                </Grid>
              </Grid>   
             
              <Typography sx={{ fontSize: 12, marginTop: "16px" }} color="text.secondary" gutterBottom>
              My Diamond Team is a high reward, low risk contract on binance smart chain that is paying out 1.5% daily return on investment up to 365%.
Investors can compound and extend their earnings through deposits, and reinvestments rewards as well as through team based referrals.<br/><br/>
Using Smart Contracts, Tokens, and Cryptocurrency in general is always a risk. DYOR before investing.
              </Typography>

            </Paper>
          </Grid>

          <Grid item xs={12} md={3}>
            <Paper className={"minHeight"} style={{padding: "16px"}}>
            <Typography sx={{ fontSize: 16 }} color="secondary" gutterBottom>
                Start Investing:
              </Typography>
              <Typography variant="h2" sx={{ display: this.state.referredBy.length > 1 && this.state.referredBy != "Will show after deposit"? "none":"block"}}>
              {/* mainnet: 0x41b7E9c1eCC54Ef5458Fc239A2F837D1f8E9E5Ce */}
                <TextField inputRef={this.sponsorInput} value={this.state.sponsorAddress} onChange={(event) => this.setState({ sponsorAddress: event.target.value })} label={"Your Sponsor"} type="text" fullWidth={true}></TextField>
              </Typography>
              <Typography variant="h2">
                <TextField inputRef={this.amountInput} inputProps={{min: 0.1, max: 25}} defaultValue={0.1} label={"Amount in BNB"} type="number" fullWidth={true}></TextField>
              </Typography>
              <Button sx={{my: 1}} disabled={window.connected != true} size="small" variant="contained" onClick={this.onClick_Invest} fullWidth={true}>deposit</Button>
              <Typography sx={{ fontSize: 12, textAlign: "right" }} color="text.secondary" gutterBottom>
                Balance:
              </Typography>
              <Typography sx={{ fontSize: 12, textAlign: "right" }} color="text.primary" gutterBottom>
                {formatEthFromWei(this.state.balance, 6)} BNB
              </Typography>
              
              <Typography sx={{ fontSize: 16 }} color="secondary" gutterBottom>
                Personal Statistics:
              </Typography>
            
              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Next Income(1.5%):
              </Typography>
              <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
                  {this.state.nextIncomeCountdown}
              </Typography>
              
              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Estimated 1.5% Daily Earnings:
              </Typography>
              <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
                  {formatNumber(this.state.dailyIncomeExpected, 6)} BNB
              </Typography>

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Accumulated Income:
              </Typography>
              <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
                  {formatEthFromWei(this.state.dailyIncome, 6)} BNB available
              </Typography>

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Direct Referral Income:
              </Typography>
              <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
                  {formatEthFromWei(this.state.directReferralIncome, 6)} BNB
              </Typography>

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Matching Bonus:
              </Typography>
              <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
                  {formatEthFromWei(this.state.matchingBonus, 6)} BNB
              </Typography>

              
              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                TOP Sponsor Rewards:
              </Typography>
              <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
                  {formatEthFromWei(this.state.topSponsorRewards, 6)} BNB
              </Typography> 

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Your Address:
              </Typography>
              <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" className={"truncate-text"} gutterBottom>
                {this.state.yourAddress}
              </Typography>

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Referred By:
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.primary" className={"truncate-text"} gutterBottom>
                {this.state.referredBy}
              </Typography>
            </Paper>
          </Grid>

          <Grid item xs={12} md={3}>
            <Paper className={"minHeight"} style={{padding: "16px"}}>

              <Typography sx={{ fontSize: 16 }} color="secondary" gutterBottom>
                Smart Contract Statistics:
              </Typography>

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Total Members:
              </Typography>
              <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
                {this.state.totalUsers}
              </Typography>

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Total BNB Deposits All Time:
              </Typography>
              <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
                {formatEthFromWei(this.state.totalDeposits, 6)} BNB
              </Typography>

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Total Withdrawn By Members:
              </Typography>
              <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
                {formatEthFromWei(this.state.withdrawnByMembers, 6)} BNB
              </Typography>

{/*               <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Total Reinvested By Members:
              </Typography>
              <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
                {formatEthFromWei(this.state.totalReinvested, 6)} BNB
              </Typography> */}

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Top Sponsor Reward Pool:
              </Typography>
              <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
                {formatEthFromWei(this.state.topSponsorRewardPool, 6)} BNB
              </Typography>

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Next Top Sponsor Pool Reward:
              </Typography>
              <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
                {this.state.topSponsorRewardPoolCountdown}
              </Typography>

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Maximum Amount From Top Referrer:
              </Typography>
              <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
                {formatEthFromWei(this.state.maximumAmountFromTopReferrer, 6)} BNB
              </Typography>

              <Typography sx={{ fontSize: 16 }} color="secondary" gutterBottom>
                User totals:
              </Typography>


              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
              Total Amount Of Deposited BNB:
              </Typography>
              <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
                  {formatEthFromWei(this.state.totalAmountOfDepositedBNB, 6)} BNB
              </Typography>  

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
              Total Amount Of Received BNB:
              </Typography>
              <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
                  {formatEthFromWei(this.state.totalAmountOfReceivedBNB, 6)} BNB
              </Typography>  

              <Typography sx={{ fontSize: 16 }} color="secondary" gutterBottom>
                Organization:
              </Typography>

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Personal Invited Partners:
              </Typography>
              <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
                  {this.state.personalInvitedPartners}
              </Typography> 

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
              Total Number Of Partners:
              </Typography>
              <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
                  {this.state.totalNumberOfPartners}
              </Typography>   

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
              All downline’s investments:
              </Typography>
              <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
                  {formatEthFromWei(this.state.allDownlinesInvestments, 6)} BNB
              </Typography>  

            </Paper>
          </Grid>

          <Grid item xs={12} md={3}>
            <TeamView defaultname={"My Referral Team"} className={"minHeight-small"} style={{paddingTop: "16px", paddingBottom: "16px"}} teamId={this.state.referral_team}></TeamView>

            <TeamView defaultname={"My Upline Team"} className={"minHeight-small"} style={{marginTop: "16px", paddingTop: "16px", paddingBottom: "16px"}} teamId={this.state.upline_team}></TeamView>
          </Grid>


          <Grid item xs={12} md={4}>
                <AirdropPanel balance={this.state.balance}></AirdropPanel>
          </Grid>

          <Grid item xs={12} md={4}>
            <Paper className={"minHeight-small"} style={{padding: "16px"}}>
              <Typography sx={{ fontSize: 16 }} color="secondary" gutterBottom>
                Airdrop Statistics:
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Last Airdrop Sent:
              </Typography>
              <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
                  {this.state.myLastAirdropSent}
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Total Airdrops Sent:
              </Typography>
              <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
                  {formatEthFromWei(this.state.myAirdrops, 6)} BNB
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Total Airdrop Received:
              </Typography>
              <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
                  {formatEthFromWei(this.state.myTotalReceivedAirdrops, 6)} BNB
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Contracts Total Airdrops:
              </Typography>
              <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" gutterBottom>
                  {formatEthFromWei(this.state.totalAirdrops, 6)} BNB
              </Typography>

            </Paper>
          </Grid>

          <Grid item xs={12} md={4}>
            <Paper className={"minHeight-small"} style={{padding: "16px"}}>
            <Grid container>
                    <Grid item xs={12} md={12} sx={{p: 1}}>
                      <Button fullWidth={true} variant={"outlined"} color={"secondary"} sx={{my: 1, marginRight: 1}} size="small" onClick={this.onClick_VerifiedContract}><BUSDIcon style={{height: "30px",  marginRight: "8px"}}></BUSDIcon>Verified Contract</Button>
                    </Grid>
                    <Grid item xs={12} md={12} sx={{p: 1}}>
                      <Button fullWidth={true} variant={"outlined"} color={"secondary"} sx={{my: 1, marginRight: 1}} size="small" onClick={this.onClick_Telegram}><TelegramIcon style={{height: "30px", marginRight: "8px"}}></TelegramIcon>MyDeFi-Team Telegram</Button>
                    </Grid>
                    <Grid item xs={12} md={12} sx={{p: 1}}>
                      <Button fullWidth={true} variant={"outlined"} color={"secondary"} sx={{my: 1, marginRight: 1}} size="small" onClick={this.onClick_Audit}><ShieldIcon style={{height: "30px", marginRight: "8px"}}></ShieldIcon>Security Audit</Button>
                    </Grid> 
                    <Grid item xs={12} md={12} sx={{p: 1}}>
                      <Button fullWidth={true} variant={"outlined"} color={"secondary"} sx={{my: 1, marginRight: 1}} size="small" onClick={this.onClick_DownloadLitepaper}><PictureAsPdfIcon style={{height: "30px", marginRight: "8px"}}></PictureAsPdfIcon>Litepaper</Button>
                    </Grid> 
              </Grid>
            </Paper>
          </Grid>

{/*           <Grid item xs={12} md={12}>
            <TeamPanel></TeamPanel>
          </Grid>
 */} 
          <Grid item xs={12} md={12}>
            <Paper style={{padding: "16px"}}>
            <Typography sx={{ fontSize: 16 }} color="secondary" gutterBottom>
              Guide:
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
              My Diamond Team – BNB Bank Roll Project is decentralized community support
              fund built on BSC blockchain and smart contract technology.<br/><br/>
              The My Diamond Team Smart Contract is designed to provide everyone
              with an independent, financial support fund, based on Smart Contract
              technology, surrounded, and supported by multiple services including DEFI
              and online gaming.<br/><br/>
               
              Any participant can contribute BNB towards the community fund and
              support the community members, the participant will now activate the
              contract code and will start to receive support back from other community
              members.<br/><br/>
               
              It is 100% decentralized and a community-based project; Meaning that
              there are no guarantee nor additional profits made in the project alone, all
              that you receive from the project is received from other community
              members and based upon your own and the community efforts as this is a
              P2P support model.<br/><br/>
               
              You support others and others support you back.<br/><br/>
              My Diamond Team can be viewed as a decentralized “third party” which
              insures complete fairness, transparency, and justice for all according to the
              algorithm code.<br/><br/>
               
              There are no mediators or intermediaries, no owners, no company, no
              CEO, and no human interaction, which is effectively guaranteeing the
              fairness of all participating partners and partners funds according to the
              rules of the Smart Contract.<br/><br/>
              It is the most reasonable, safest, and best cutting-edge financial SUPPORT
              model available currently.<br/><br/><br/>  
              </Typography>

              <Typography sx={{ fontSize: 16 }} color="secondary" gutterBottom>
              How To Start:
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
              You can participate in the My Diamond Team Project by depositing a
              minimum of 0.1 BNB to the Fund, you are now eligible to receive
              365% back.<br/><br/>
               
              The 365% is returned in 4 ways (1 passive and 3 via
              marketing/invites) and when the 365% is accumulated through any of
              the 4 ways, you can reinvest or make new deposits from your
              earnings to increase your daily 1.5% rate per day and your 365%
              total income.<br/><br/>
               
              1. 1.5% Daily return on your Deposit,100% Passive.<br/>
               
              2. 5% Direct Referral Commission for Sharing and Growing the
              Community Fund.<br/>
               
              3. Matching Commission on Partners Daily Income every time they
              make a WITHDRAWAL.<br/><br/>
               
              Level 1 - 5 = 10% commission per downline withdrawal.<br/>
              Level 6 to 10 = 7% commission per downline withdrawal.<br/>
              Level 11 to 15 = 5% commission per downline withdrawal.<br/>
               
              * 1 new level is activated for each direct partner, maximum 15
              levels, see above.<br/><br/>
               
              4. Daily Top Referrer Pool 3%, of ALL Deposits set aside in pool,
              every 24 hour 7% of the pool is shared among top 4 sponsors in
              volume.<br/><br/>
              Pool Distribution: Top 1 - 2.5%, Top 2 - 2.0%, Top 3 - 1.5%, Top
              4 - 1.0%<br/><br/>
               
              5. 10% fee for investing and withdrawing.<br/><br/>
               
              6. Minimum deposit to join is 0.1 BNB and a maximum of 25 BNB total deposit
              per wallet is also implemented as an anti-whale measure.<br/><br/>
               
              7. User can withdraw their payout every time. It will
              be accumulative, once 24hrs has passed then you will receive a total of 1.5% income.<br/><br/>
               
              8. Re-invest/Compound is also implemented, so investors would not have to withdraw their daily earnings
               and deposit it again which causes more gas fees.<br/><br/>
               
              9. Airdrop, Users can send any amount of bnb from their wallet to another user 
              and that amount will directly be invested for the receiving address.<br/><br/> 
               
              10. Team, whenever an investor uses a referral link a new team will be created, making the upline the team owner.
              The referral team's name can then be changed to the desired team name. There is no maximum members for a team as long
              as it is a direct referral.<br/><br/>
               
              11. Nicknames, users can assign a nickname for them that they can use in exchange of their address to be put in the referral link.
              Ensuring safety for those investors who do not feel comfortable giving away their addresses.<br/><br/><br/>
               
              </Typography>
              <Typography sx={{ fontSize: 16 }} color="secondary" gutterBottom>
              Withdrawal Taxes:
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
              Withdrawal taxes in the project is percentage
              based, meaning that the taxes per withdrawal will be based on how
              many percent of it is in the contract.<br/><br/> 
              See breakdown of fees below:<br/><br/>
              ▫️ 0% tax if amount is &lt;   1% of contract balance<br/>
              ▫️ 5% tax if amount is &gt;=  1% of contract balance<br/>
              ▫️10% tax if amount is &gt;=  2% of contract balance<br/>
              ▫️15% tax if amount is &gt;=  3% of contract balance<br/>
              ▫️20% tax if amount is &gt;=  4% of contract balance<br/>
              ▫️25% tax if amount is &gt;=  5% of contract balance<br/>
              ▫️30% tax if amount is &gt;=  6% of contract balance<br/>
              ▫️35% tax if amount is &gt;=  7% of contract balance<br/>
              ▫️40% tax if amount is &gt;=  8% of contract balance<br/>
              ▫️45% tax if amount is &gt;=  9% of contract balance<br/>
              ▫️50% tax if amount is &gt;= 10% of contract balance<br/><br/>
              * LEGEND:<br/>
              = - Equal to<br/>
              &lt; - Less Than or<br/>
              &gt; - Greater Than or<br/><br/><br/>  
              </Typography>
              <Typography sx={{ fontSize: 16 }} color="secondary" gutterBottom>
              Important Notes:
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
              * This project is the latest release of My Defi Team.<br/><br/>
              * My Diamond Team is a community-based project, 100% decentralized,
              P2P transactions through Smart Contract.<br/><br/>
              * Each Deposit Cycle is 365%.<br/><br/>
              * When 365% is received, User should make a new deposit to increase the their max payout
              and continue earning passively.<br/><br/>
              * Minimum investment is 0.1 BNB, open to all participants, no restrictions.<br/><br/>
              * The default address as the primary sponsor will be "0xfDC01397731d3280bBB5e2A136381297962C87d2"
                this is the marketing address of the project which all the gathered funds will be used to further grow the project.<br/><br/>
              * No referrals required to receive the 1.5% daily pay out, 100%
              passive. Referrals will just help the investor reach the 365% if they wish not to compound.<br/><br/>
              * Increase the speed of your returns by growing the community fund through
              marketing / sharing of the project.<br/><br/>
              * WE RECOMMEND TO DOWNLOAD OUR LITE PAPER FOR A MORE DETAILED DOCUMENTATION REGARDING THE PROJECT.<br/><br/><br/>  
              </Typography>
            </Paper>
          </Grid>
        </Grid>
        

        <Modal
          open={this.state.modalOpen}
         /*  onClose={this.setModalClose} */
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
          disableEscapeKeyDown={true}

        >
          <Box sx={modalStyle}>
            <Typography id="modal-modal-title" variant="h6" component="h2" style={{textAlign: 'center'}}>
              <Countdown className='countdown' date={this.CountDownDate} />
            </Typography>

                <div style={{display: "flex", justifyContent: "center", alignItems:"center"}}>
                    <img style={{width: "300px"}} src={diamondteamlogo}/>
                </div>
                <Button fullWidth={true} variant={"outlined"} color={"secondary"} sx={{my: 1, marginRight: 1}} size="small" onClick={this.onClick_Telegram}><TelegramIcon style={{height: "30px", marginRight: "8px"}}></TelegramIcon>MyDeFi-Team Telegram</Button>
          </Box>
        </Modal>
      </Box>
    );
  }
}

export default App;
